"use client";

import Image from "next/image";
import { BarChart, CreditCard, Package, Star, TrendingDown, TrendingUp, User } from "lucide-react";
import { Montserrat } from "next/font/google";

const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["500", "800"],
  display: "swap",
});

export default function SellerDashboard() {
  // Données simulées pour le vendeur
  const seller = {
    firstName: "Ahmed",
    lastName: "Benali",
    accountNumber: "VEN-123456789",
    profileImage: "/seller-avatar.jpg", // Remplace par une URL ou un placeholder
  };

  const stats = {
    totalSales: "15 000 €",
    topProduct: { name: "T-shirt Premium", revenue: "5 000 €" },
    stockCount: 250,
    bestRatedProduct: { name: "Chaussures Sport", rating: 4.8 },
    worstRatedProduct: { name: "Sac Basique", rating: 2.5 },
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        {/* Section Bienvenue et Carte Vendeur */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
          {/* Section Bienvenue */}
          <div className="bg-gradient-to-r from-teal-400 to-teal-600 rounded-xl p-8 text-white shadow-lg">
            <h1 className={`text-3xl font-extrabold tracking-tight ${montserrat.className}`}>
              Bienvenue, {seller.firstName} {seller.lastName}
            </h1>
            <p className="text-lg mt-2">Journée pleine de bonheur</p>
          </div>

          {/* Carte Vendeur */}
          <div className="bg-white rounded-xl shadow-lg p-6 flex items-center gap-4">
            {/* Image du vendeur */}
            <div className="w-24 h-24 rounded-full overflow-hidden bg-gray-200 flex items-center justify-center border-2 border-gray-300">
              {seller.profileImage ? (
                <Image
                  src={seller.profileImage}
                  alt="Profil Vendeur"
                  width={96}
                  height={96}
                  className="object-cover w-full h-full"
                />
              ) : (
                <User className="h-12 w-12 text-gray-500" />
              )}
            </div>

            {/* Informations et Code QR */}
            <div className="flex-1">
              <h2 className="text-xl font-semibold text-gray-900">Carte Vendeur</h2>
              <p className="text-gray-600 mt-1">
                <span className="font-medium">Nom :</span> {seller.lastName}
              </p>
              <p className="text-gray-600">
                <span className="font-medium">Prénom :</span> {seller.firstName}
              </p>
              <p className="text-gray-600">
                <span className="font-medium">Numéro de compte :</span> {seller.accountNumber}
              </p>
              <div className="mt-2">
                <Image
                  src="/qr-code-placeholder.png" // Remplace par un vrai QR code si nécessaire
                  alt="QR Code"
                  width={80}
                  height={80}
                  className="border border-gray-200 rounded-md"
                />
              </div>
            </div>
          </div>
        </div>

        {/* Statistiques */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
          {/* Total des ventes */}
          <div className="bg-white rounded-xl shadow-lg p-6 flex items-center gap-4">
            <CreditCard className="h-8 w-8 text-teal-600" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900">Total des ventes</h3>
              <p className="text-2xl font-bold text-gray-700 mt-1">{stats.totalSales}</p>
            </div>
          </div>

          {/* Produit le plus rentable */}
          <div className="bg-white rounded-xl shadow-lg p-6 flex items-center gap-4">
            <TrendingUp className="h-8 w-8 text-teal-600" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900">Produit le plus rentable</h3>
              <p className="text-gray-600 mt-1">{stats.topProduct.name}</p>
              <p className="text-xl font-bold text-gray-700">{stats.topProduct.revenue}</p>
            </div>
          </div>

          {/* Nombre de produits en stock */}
          <div className="bg-white rounded-xl shadow-lg p-6 flex items-center gap-4">
            <Package className="h-8 w-8 text-teal-600" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900">Nombre de produits</h3>
              <p className="text-2xl font-bold text-gray-700 mt-1">{stats.stockCount}</p>
            </div>
          </div>
        </div>

        {/* Meilleur et Pire produit noté */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6 mb-8">
          {/* Meilleur produit noté */}
          <div className="bg-white rounded-xl shadow-lg p-6 flex items-center gap-4">
            <Star className="h-8 w-8 text-yellow-500" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900">Meilleur produit noté</h3>
              <p className="text-gray-600 mt-1">{stats.bestRatedProduct.name}</p>
              <p className="text-xl font-bold text-gray-700">{stats.bestRatedProduct.rating}/5</p>
            </div>
          </div>

          {/* Pire produit noté */}
          <div className="bg-white rounded-xl shadow-lg p-6 flex items-center gap-4">
            <TrendingDown className="h-8 w-8 text-red-500" />
            <div>
              <h3 className="text-lg font-semibold text-gray-900">Pire produit noté</h3>
              <p className="text-gray-600 mt-1">{stats.worstRatedProduct.name}</p>
              <p className="text-xl font-bold text-gray-700">{stats.worstRatedProduct.rating}/5</p>
            </div>
          </div>
        </div>

        {/* Courbe analytique */}
        <div className="bg-white rounded-xl shadow-lg p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
            <BarChart className="h-6 w-6 text-teal-600" />
            Analyse des ventes
          </h3>
          <div className="h-64 bg-gray-100 rounded-lg flex items-center justify-center">
            <p className="text-gray-500">
              [Placeholder pour la courbe analytique - Utiliser une bibliothèque comme Chart.js]
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}